@import '@materializecss/materialize/sass/materialize';

$spacings: (1, 2, 3, 4, 5, auto);

$sides: (
        "t": "top",
        "b": "bottom",
        "l": "left",
        "r": "right",
        "": (
                "top",
                "left",
                "bottom",
                "right"
        ),
        "x": (
                "left",
                "right"
        ),
        "y": (
                "top",
                "bottom"
        )
);

$breakpoints: (
        "": "",
        "xs": 576px,
        "sm": 768px,
        "md": 992px,
        "lg": 1200px,
        "xl": 1400px
);

@each $breakName, $breakValue in $breakpoints {
  @each $space in $spacings {
    @each $prefix, $positions in $sides {
      @if $breakName != "" {
        $prefix: #{$prefix}-#{$breakName};
      } @else {
        $prefix: #{$prefix};
      }

      @if $breakName != "" {
        @media (min-width: $breakValue) {
          .p#{$prefix}-#{$space} {
            @each $pos in $positions {
              padding-#{$pos}: if($space == auto, $space, #{$space * 0.25}rem);
            }
          }
          .m#{$prefix}-#{$space} {
            @each $pos in $positions {
              margin-#{$pos}: if($space == auto, $space, #{$space * 0.25}rem);
            }
          }
        }
      } @else {
        .p#{$prefix}-#{$space} {
          @each $pos in $positions {
            padding-#{$pos}: if($space == auto, $space, #{$space * 0.25}rem);
          }
        }
        .m#{$prefix}-#{$space} {
          @each $pos in $positions {
            margin-#{$pos}: if($space == auto, $space, #{$space * 0.25}rem);
          }
        }
      }
    }
  }
}