// Badges
span.badge {
  min-width: 3rem;
  padding: 0 6px;
  margin-left: 14px;
  text-align: center;
  font-size: 1rem;
  line-height: $badge-height;
  height: $badge-height;
  color: $badge-color;
  float: right;
  box-sizing: border-box;

  &.new {
    font-weight: 300;
    font-size: 0.8rem;
    color: $badge-color-new;
    background-color: $badge-bg-color;
    border-radius: 2px;
  }
  &.new:after {
    content: " new";
  }

  &[data-badge-caption]::after {
    content: " " attr(data-badge-caption);
  }
}

.active span.badge {
  color: $badge-color-active;
}

// Special cases
nav ul a span.badge {
  display: inline-block;
  float: none;
  margin-left: 4px;
  line-height: $badge-height;
  height: $badge-height;
  -webkit-font-smoothing: auto;
}

// Line height centering
.collection-item span.badge {
  margin-top: calc(#{$collection-line-height * 0.5} - #{$badge-height * 0.5});
}

.collapsible span.badge {
  margin-left: auto;
}

.collapsible .active span.badge:not(.new) {
  color: $badge-color;
}

.sidenav span.badge {
  margin-top: calc(#{$sidenav-line-height * 0.5} - #{$badge-height * 0.5});
}

table span.badge {
  display: inline-block;
  float: none;
  margin-left: auto;
}
